import React from "react";
import PropTypes from "prop-types";

import st from "./style";

class DefaultButton extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      hover: false
    };
  }

  handleClick = () => {
    if (this.props.disabled) {
      return;
    }
    this.props.onClick();
  };

  onMouseOver = () => {
    this.setState({
      hover: true
    });
  };

  onMouseOut = () => {
    this.setState({
      hover: false
    });
  }; 

  render() {
    
    const CustomTag = this.props.type || 'button';

    return (
      <CustomTag
        onClick={this.handleClick}
        onMouseOver={this.onMouseOver}
        onMouseOut={this.onMouseOut}
        style={{
          ...st.container(this.props, this.state),
          ...this.props.style
        }}
        href={this.props.href}
      >
        <span style={st.label(this.props)}>{this.props.label}</span>
      </CustomTag>
    );
  }
}

DefaultButton.propTypes = {
  colorName: PropTypes.string, // pink || pool
  label: PropTypes.string,
  onClick: PropTypes.func,
  fullWidth: PropTypes.bool,
  disabled: PropTypes.bool,
  type: PropTypes.string,//button || a || Link
  href: PropTypes.string,
  style: PropTypes.object,
};
DefaultButton.defaultProps = {
  colorName: "pink",
  label:'',
  onClick: () => {},
  fullWidth: false,
  disabled: false,
  type: 'button',
  href: null,
  style: {},
};

export default DefaultButton;
