import Color from "color";

import Colors from "../../style/Colors";

const getColor = ($props, $state) => {
  const color = Colors[$props.colorName];
  if ($props.disabled) {
    return Color(color).lighten(0.2);
  }
  if ($state.hover) {
    // return Color(color).darken(0.3);
  }
  return color;
};

export default {
  container: ($props, $state) => ({
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    position: 'relative',
    minHeight: 60,
    backgroundColor: getColor($props, $state),
    border: 0,
    borderColor: getColor($props, $state),
    borderWidth: 1,
    borderRadius: 30,
    width: $props.fullWidth ? "100%" : "auto",
    margin: 'auto',
    cursor: $props.disabled ? "default" : "pointer",
  }),
  label:($props) => ({
    fontFamily: 'Muli-Regular',
    color: $props.disabled?Colors.lightGrey10:Colors.white,
    fontSize: 18,
    marginLeft: 40,
    marginRight: 40,
  })
};

